import React from 'react';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import SearchIcon from '@material-ui/icons/Search';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import { makeStyles } from '@material-ui/core/styles';
import IconButton from '@material-ui/core/IconButton';

const useStyles = makeStyles(() => ({
    iconButton: {
        padding: 0
    },
    listItemIcon: {
        minWidth: 0
    },
    bold: {
        fontWeight: 'bold'
    }
}));

export default function MaterialLocalizationComponent(props) {
    const classes = useStyles();

    const onSearch = (materialId) => {
        if (props.onSearch) props.onSearch(materialId);
    }

    const isSelected = (material) => {
        return props.selectedMaterial !== undefined && props.selectedMaterial === material.key;
    }

    const getMaterialsSorted = () => {
        return props.materials.sort((a, b) => {
            if(a.name < b.name) { return -1; }
            if(a.name > b.name) { return 1; }
            return 0;
        });
    }

    return (
        <List>
            {getMaterialsSorted().map((material) => {
                return <ListItem key={material.key}>
                    <ListItemText className={isSelected(material) ? 'selected-material' : ''}>{material.name}</ListItemText>
                    <ListItemIcon className={classes.listItemIcon}>
                        <IconButton className={classes.iconButton}>
                            <SearchIcon className={isSelected(material) ? 'selected-material' : ''} onClick={() => onSearch(material.key)} />
                        </IconButton>
                    </ListItemIcon>
                </ListItem>
            })}
        </List>
    );
}


