/**
 * Maps the zone ID from the database with the ID in the SvgMap.
 */
export default function localZonesMap() {
    var _map = new Map();
    _map.set("zone1", 1);
    _map.set("zone2", 2);
    _map.set("zone3", 3);
    _map.set("zone4", 4);
    _map.set("zone5", 5);
    _map.set("zone6", 6);
    _map.set("zone7", 7);
    _map.set("zone8", 8);
    _map.set("zone9", 9);
    _map.set("zone10", 10);
    _map.set("zone11", 11);
    _map.set("zone12", 12);

    return {
        map: _map
    };
}


